import React, { useContext, useState, useEffect } from "react";
import WordContext from "../contexts/WordContext";

const Guess = (props) => {
    const [guess,setGuess] = useState(props.guess.toUpperCase());
    const {answer, correct, setCorrect, setLetterBackground, letterBackground} = useContext(WordContext);
    
    
    useEffect(()=> {
    for(let i = 0; i <= 5; i ++){
        if (answer[i]===guess[i]){
            setCorrect(true);
            continue;
        }
        else if (answer[i]!==guess[i]){
            setCorrect(false);
            break;
        }
    }
    },[])

    

    return (
        <div className="guesscontainer mx-auto">
        <div className="guess mx-auto d-flex my-2 justify-content-between">
        {   
            guess.split("").map((letter,i)=>{
                const style = {
                    backgroundColor: answer[i] === guess[i] ? "rgb(108,172,100)" : answer.includes(guess[i]) ? "rgba(201,180,88,255)" : "background-color: rgba(120,124,126,255);"
                }
                
                return <div style={style} key={i} className="animate__animated animate__flipInX animate__slower letter">{letter}</div>
            })
        }
        </div>
        
        </div>
    )
}

export default Guess;