import React, {useEffect, useState} from "react";
import axios from "axios";
import Form from '../components/Form'
import WordContext from '../contexts/WordContext'
import Guess from '../components/Guess';
import SpacesToGuess from "../components/SpacesToGuess";
import Keyboard from "../components/Keyboard";


const Classic = (props) => {
    const [form, setForm] = useState("");

    var randomWord = require('random-word-by-length')
    let randomAnswer = ""
    const [valid, setValid] = useState(false);
    while (randomAnswer.length < 5){
        randomAnswer = randomWord(5)
    }

    useEffect(() => {
        axios.get("https://wordsapiv1.p.rapidapi.com/words/" + randomAnswer +"/?rapidapi-key=ad3b56b37cmsh87d7fec1bbe6baep1c178ejsn60c18aafc1b0")
        .then(res=>{
            console.log(res);
            setValid(true)
        })
        .catch(err=> {console.log(err)
            setValid(false)
        })
    },[]);
    
    randomAnswer = randomAnswer.toUpperCase();
    const [answer,setAnswer] = useState(randomAnswer);

    const [guesses, setGuesses] = useState([]);
    const [correct, setCorrect] = useState(false);
    const [spaces, setSpaces] = useState([1,2,3,4,5,6])
    const [letterBackground, setLetterBackground] = useState({
        A : 0,
        B : 0,
        C : 0,
        D : 0,
        E : 0,
        F : 0,
        G : 0,
        H : 0,
        I : 0,
        J : 0,
        K : 0,
        L : 0,
        M : 0,
        N : 0,
        O : 0,
        P : 0,
        Q : 0,
        R : 0,
        S : 0,
        T : 0,
        U : 0,
        V : 0,
        W : 0,
        X : 0,
        Y : 0,
        Z : 0
    });

    return (
    <div className="App">
        <WordContext.Provider value={{form, setForm, answer, guesses, setGuesses, correct, setCorrect, spaces, setSpaces, letterBackground, setLetterBackground}}>
        <div className="content">
            {
                correct && <h2 className="my-5">Nice job! You win!</h2> 
            }    
        {
            guesses.map((item,i) => {
                return <Guess guess={item} key={i}/>
            })
        }
        {
            spaces.map((item,i) => {
                return <SpacesToGuess key={i}/>
            })
        }
        {
            !correct && <Form/>
        }
        <Keyboard/>
        </div>
        </WordContext.Provider>
    </div>
    );
}


export default Classic;