import React, { useContext, useState, useEffect } from 'react'
import WordContext from '../contexts/WordContext';
import axios from "axios";


const Form = (props) => {
    const {form,setForm, guesses, setGuesses, answer, correct, setCorrect, spaces, setSpaces, letterBackground, setLetterBackground} = useContext(WordContext);
    const [error, setError] = useState(false)



    const onSubmitHandler = (event) => {
        event.preventDefault();
        let finished = false
        let tempform = form;
        console.log("this is temp form" + tempform)

        for(let i = 0; i <= 5; i ++){
            if (answer[i]===form[i]){
                finished = true;
            }
            else if (answer[i]!==form[i]){
                finished = false;
                break;
            }
        }

        if (finished) {
            setCorrect(true);
            setGuesses([...guesses,form])

            const copyState = [...spaces];
            copyState.splice(0,1);
            setSpaces(copyState);
        }
        else {
            // console.log("in the else part")
            axios.get("https://wordsapiv1.p.rapidapi.com/words/" + form +"/?rapidapi-key=ad3b56b37cmsh87d7fec1bbe6baep1c178ejsn60c18aafc1b0")
            .then(res=>{
                console.log(res);
                setGuesses([...guesses,form])

            
                const copyState = [...spaces];
                copyState.splice(0,1);
                setSpaces(copyState);

                const copyLetterBackground = {...letterBackground};
        
                for(let j = 0; j < 5; j ++){
                    // console.log(`Checking answer letter ${answer[j]} with guess letter ${form[j]}`);
                    if (answer[j]===tempform[j]){
                        console.log(`In the iffy my biffy ---> ${answer[j]} : 3`);
                        copyLetterBackground[form[j]] = 3
                    }
                    else if (answer.includes(tempform[j])){
                        console.log("in the else if")
                        copyLetterBackground[form[j]] = 2
                    }
                    else {
                        copyLetterBackground[form[j]] = 1
                    }
                }
                setLetterBackground(copyLetterBackground);

                setError(false)
                setForm("")
            })
            .catch(err=> {
                console.log(err)
                setError(true)
            })
        }

        
}
    
    return(
        <div>
            {
                error && <p>Please enter a valid word</p>
            }
            {
                guesses.length === 6 && !correct ? <p>{`Sorry, better luck next time. The word was ${answer}`}</p> :
            <form className='w-25 mx-auto m-5' onSubmit={onSubmitHandler}>
            <input value={form} minLength={5} maxLength={5} onChange={(event)=>setForm(event.target.value.toUpperCase())}type="text" placeholder="What is your guess?" className = 'w-100 form-control' />

            <input type="submit" className="btn btn-info d-block mx-auto my-3"/>
        </form>
        }
        
        </div>
    )
}

export default Form;